// –¢–∏–ø—ã –¥–ª—è —Ä–∞–±–æ—Ç—ã —Å –∫–æ–º–º–µ—Ä—á–µ—Å–∫–∏–º–∏ –ø—Ä–µ–¥–ª–æ–∂–µ–Ω–∏—è–º–∏ (Proposals)

export type PricingMode = "single" | "variants";

export interface ProposalItem {
  id: string;
  productId?: string; // –ï—Å–ª–∏ —Ç–æ–≤–∞—Ä –∏–∑ –∫–∞—Ç–∞–ª–æ–≥–∞
  name: string;
  description?: string;
  qty: number;
  price: number;
  discount?: number; // –í –ø—Ä–æ—Ü–µ–Ω—Ç–∞—Ö
  unit?: string; // "—à—Ç", "—á–∞—Å", "–º–µ—Å—è—Ü" –∏ —Ç.–¥.
}

export interface ProductVariantGroupRow {
  id: string;
  type: "group";
  title: string;
}

export interface ProductVariantItemRow extends ProposalItem {
  type: "item";
  groupId?: string;
}

export type ProductVariantRow = ProductVariantGroupRow | ProductVariantItemRow;

export interface ProductVariant {
  id: string;
  name: string;
  description?: string;
  isRecommended?: boolean;
  currency?: string;
  rows: ProductVariantRow[];
}

export interface ProductsViewSettings {
  showDiscountColumn?: boolean;
  showUnitColumn?: boolean;
}

export interface AdvantageItem {
  id?: string;
  iconUrl?: string;
  title: string;
  description: string;
}

