  recipientName?: string; // "–£–≤–∞–∂–∞–µ–º—ã–π –ò–≤–∞–Ω –ü–µ—Ç—Ä–æ–≤–∏—á!"

  // –ö–æ–Ω—Ç–µ–∫—Å—Ç –∏ —Ä–µ—à–µ–Ω–∏–µ
  problemDesc?: string; // –û–ø–∏—Å–∞–Ω–∏–µ —Å–∏—Ç—É–∞—Ü–∏–∏ –∫–ª–∏–µ–Ω—Ç–∞ (Point A)
  solutionDesc?: string; // –ü—Ä–µ–¥–ª–∞–≥–∞–µ–º–æ–µ —Ä–µ—à–µ–Ω–∏–µ (Point B)
  additionalDesc?: string; // –î–æ–ø–æ–ª–Ω–∏—Ç–µ–ª—å–Ω–æ–µ –æ–ø–∏—Å–∞–Ω–∏–µ

