  productVariants?: ProductVariant[];
  activeVariantId?: string;
  productsView?: ProductsViewSettings;

  // –£—Å–ª–æ–≤–∏—è
  deadline?: string; // –¢–µ–∫—Å—Ç–æ–≤–æ–µ –ø–æ–ª–µ: "2 –Ω–µ–¥–µ–ª–∏", "–¥–æ 31.12.2024"
  paymentTerms?: string; // "prepaid" | "50-50" | "postpaid" | "custom"
  paymentCustom?: string; // –ï—Å–ª–∏ paymentTerms = "custom"
  validUntil?: Date | string; // –°—Ä–æ–∫ –¥–µ–π—Å—Ç–≤–∏—è –ö–ü

  // –ù–î–°
  includeVat: boolean;
  vatRate: number; // –í –ø—Ä–æ—Ü–µ–Ω—Ç–∞—Ö

  // –ì–∞–ª–µ—Ä–µ—è
  galleryImages?: string[]; // –ú–∞—Å—Å–∏–≤ URL –∏–∑–æ–±—Ä–∞–∂–µ–Ω–∏–π

  // –ü—Ä–µ–∏–º—É—â–µ—Å—Ç–≤–∞
  advantages?: AdvantageItem[];
  advantagesColumns?: 1 | 2 | 3;

  // –ù–∞—Å—Ç—Ä–æ–π–∫–∏ –≤–∏–¥–∏–º–æ—Å—Ç–∏ —Ä–∞–∑–¥–µ–ª–æ–≤
